.section-aboutFeatures {
    position: relative;
    background-color: #fff;
    padding: 0 0 35rem 0;
    margin-top: -2rem;
}


.composition {
    position: relative;

    &__photo {
        width: 55%;
        box-shadow: 0 1.5rem 4rem #000;
        border-radius: 2px;
        position: absolute;
        z-index: 10;
        transition: all .2s;
        outline-offset: 2rem;

        // @include respond(tab-port) {
        //     float: left;
        //     position: relative;
        //     width: 33.33333333%;
        //     box-shadow: 0 1.5rem 3rem rgba($color-black, .2);
        // }

        &--p1 {
            left: 0;
            top: -2rem;

            // @include respond(tab-port)  {
            //     top: 0;
            //     transform: scale(1.2);
            // }
        }

        &--p2 {
            right: 0;
            top: 2rem;

            // @include respond(tab-port)  {
            //     top: -1rem;
            //     transform: scale(1.3);
            //     z-index: 100;
            // }
        }

        &--p3 {
            left: 20%;
            top: 10rem;

            // @include respond(tab-port)  {
            //     top: 1rem;
            //     left: 0;
            //     transform: scale(1.1);
            // }
        }

        &:hover {
            outline: 1.5rem solid #7ed56f;
            transform: scale(1.05) translateY(-.5rem);
            box-shadow: 0 2.5rem 4rem rgba(#000, .5);
            z-index: 20;
        }

    }

    &:hover &__photo:not(:hover) {
        transform: scale(.95);
    }
